body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin:0;
  padding: 0;
  border:1 px solid red;
  vertical-align: baseline;
  background: 0 0;
  font-weight: 400;
  font-style: normal;
  text-decoration: none;
  outline: 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}
html.modal-enabled {
  overflow: hidden !important;
}
input{
  outline: none;
  border: none  ;
}
ul{
  list-style: none;
}

@font-face {
  font-family:goridita_regular;
  src: url("/steyp-clone/static/media/Gordita Regular.8f8fc889.otf");
}
@font-face {
  font-family:goridita_medium;
  src: url("/steyp-clone/static/media/Gordita Medium.2a23af3d.otf");
}
@font-face {
  font-family:goridita_bold;
  src: url("/steyp-clone/static/media/Gordita Bold.78dd2523.otf");
}

    a{
      color: #545454;
    }
    p{
      font-size: 16px;
      line-height: 1.6em;
      color: #545454;
      font-family:goridita_regular !important;

    }
    
body{
  overflow-x: hidden;
 
}
::-webkit-scrollbar {
  display: none;
}
.slick-dots {
  width: 12% !important ;
}
.typeanime{
  color: rgb(87, 194, 137) !important;
}
.slick-dots{
  width: 80px!important;
  border: 1px solid rgba(15, 167, 111, 0.13);
  margin:0px auto 0px 7px !important;
  border-radius: 27px
}
.slick-dots li.slick-active button:before
{
    opacity: .75;
    color: #3fbd7f!important;
}

